import React from 'react'
import shiftback from '../images/shiftback.png';

const Shift = () => {
  return (
    <>
        <div className="ser-main">
             <div className="shift_back">
                  <div className="shift_back1">
                    <div className='shift_back1_content'>
                         <h1>Shift4Shopy</h1>
                         <h1>Experts</h1>
                    </div>
                    <div className='shift_back1_para'>
                        <p>Flexible Enterprise-class eCommerce platform to boost <br />
                           Businesses! We offer Shift4Shop Development Services for all <br />
                        eCommerce websites at competitive prices.</p>
                    </div>
                    <button>LET’S  TALK</button>
                  </div>
                  <div className="shift_back2">
                      <img src={shiftback} alt="" />
                  </div>
             </div>
        </div>
       
    </>
  )
}

export default Shift
